import React, { Component } from "react";
import { Zoom } from 'react-slideshow-image';
import './LandingPage.scss';
  

const zoomOutProperties = {
  duration: 5000,
  transitionDuration: 500,
  infinite: true,
  indicators: true,
  scale: 0.4,
  arrows: true
}

const slideProperties = {
  duration: 5000,
  transitionDuration: 500,
  infinite: true,
  indicators: false,
  arrows: false,
  pauseOnHover: true

}

class LandingPage extends Component {
  constructor() {
    super();
    
  }

  
  render() {
  
    const images = [
      "https://drive.google.com/uc?export=view&id=1wmG-YrnwD43yGom135RWo81SKJdPz9oP",
      "https://drive.google.com/uc?export=view&id=1TFHb20dfpYJBOoExRjwJJ8ScqRr-Jcsn",
      "https://drive.google.com/uc?export=view&id=1GEMUGWrSFf8i63uePkt4iLc308QmW7um",
      "https://drive.google.com/uc?export=view&id=1CdXDLbM8ze3h_v7pDq1p57XDf5L21gaH"
    ]

    return (
      <div className={'landing-page'}>
        <div className="slide-container">
          <Zoom {...slideProperties}>
            {
              images.map((each, index) => 
              <img  className={'img'} key={index}  src={each} alt="this pic" />)
            }
          </Zoom>
        </div>
      </div>
    )
  }
}

 
export default  LandingPage;